:root {
  --bg-color-main: #f8f8f8;
  --bg-color-secondary: #fff;
  --bg-color-custom: #e6e6e6;
  --settings-color: #333;
  --btn-color: #4caf50;
  --border-color: #ccc;
  --focus-border-color: #4e9af1;
  --message-color: #dcf8c6;
}

body {
  box-sizing: border-box;
  background: var(--bg-color-main);
  justify-content: center;
  align-items: center;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.wrapper {
  border-radius: 8px;
  max-width: 480px;
  margin: 0 auto;
}

.chat {
  background-color: var(--bg-color-secondary);
  border-radius: 8px;
  animation: .3s ease-in-out slideIn;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 10px #0000001a;
}

.chat:before {
  content: "";
  opacity: .3;
  filter: blur(6px);
  background-color: #a7a5a5;
  width: 100%;
  height: 8px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.chat-header {
  background-color: var(--bg-color-custom);
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  display: flex;
}

.chat-btn {
  text-align: center;
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 4px;
  padding: 5px;
  font-size: 16px;
}

.chat-btn:hover {
  box-shadow: 0 0 1px 1px #b9b8b8;
}

.chat-btn_settings {
  color: var(--settings-color);
}

.chat-btn_exit {
  color: #d23e3e;
}

.chat__screen-wrapper {
  max-height: 400px;
  padding: 10px;
  overflow-y: scroll;
}

.chat-screen {
  flex-direction: column;
  display: flex;
}

.chat__screen-wrapper::-webkit-scrollbar {
  width: 8px;
}

.chat__screen-wrapper::-webkit-scrollbar-track {
  background: var(--bg-color-custom);
  box-shadow: 0 0 2px #0003;
}

.chat__screen-wrapper::-webkit-scrollbar-thumb {
  background: linear-gradient(#777776d3, #b4b2b2c7);
  border-radius: 10px;
}

.chat-message {
  border-radius: 8px;
  flex-direction: column;
  max-width: 90%;
  margin-bottom: 10px;
  padding: 8px;
  display: flex;
}

.message-sender {
  font-weight: bolder;
}

.chat-message_companion {
  align-self: start;
}

.chat-message_user {
  align-self: flex-end;
}

.message-text {
  word-wrap: break-word;
}

.message-time {
  color: #888;
  align-self: flex-end;
  font-size: 12px;
  font-style: italic;
}

.message_sent {
  background-color: var(--message-color);
}

.chat-send {
  background-color: var(--bg-color-custom);
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.chat-send_form {
  flex-grow: 1;
  display: flex;
}

.chat-send_input {
  border: 1px solid var(--border-color);
  border-radius: 4px;
  flex: 1;
  padding: 8px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

.chat-send_input:focus, .modal-form-input:focus {
  outline: 1px solid #bdbaba;
  box-shadow: 0 0 2px 2px #ddd;
}

.chat-btn_send {
  color: var(--bg-color-secondary);
  background-color: var(--btn-color);
  cursor: pointer;
  background-image: url("icon-send.23425526.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 22px;
  border: none;
  border-radius: 4px;
  width: 40px;
  height: 40px;
  margin-left: 8px;
  padding: 6px 12px;
  font-size: 14px;
}

.chat-btn_send:disabled {
  opacity: .6;
  cursor: not-allowed;
}

.modal {
  background-color: var(--bg-color-secondary);
  border: 0;
  border-radius: 8px;
  min-width: 280px;
  padding: 0;
  animation: .3s ease-in-out fadeIn;
  box-shadow: 0 2px 10px #0000001a;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.modal-header {
  border-radius: 8px 8px 0 0;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  display: flex;
}

.modal-title {
  margin: 0;
  font-size: 20px;
  font-weight: bold;
}

.modal-btn_close {
  appearance: none;
  cursor: pointer;
  background: url("icon-close.1d870160.svg") center / cover no-repeat;
  border: none;
  width: 20px;
  height: 20px;
  padding: 0;
}

.modal-subtitle {
  padding: 2px 16px;
  font-size: 18px;
}

.modal-form {
  flex-direction: column;
  align-items: center;
  padding: 10px 14px 16px;
  display: flex;
}

.modal-form-input {
  border: 1px solid var(--border-color);
  border-radius: 4px;
  width: 100%;
  margin-bottom: 16px;
  padding: 8px;
  font-size: 16px;
}

.modal-form-input:focus {
  border-color: var(--focus-border-color);
  outline: none;
}

.modal-form div {
  justify-content: center;
  align-items: center;
  display: flex;
}

.authorization-btn, .verification-btn, .settings-btn {
  text-align: center;
  color: var(--bg-color-secondary);
  background-color: var(--btn-color);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 8px;
  padding: 4px 16px;
  font-size: 18px;
  font-weight: lighter;
  line-height: 30px;
}

.authorization-btn_get {
  background-color: var(--btn-color);
  font-family: Roboto;
}

.authorization-btn:disabled {
  opacity: .6;
  cursor: not-allowed;
}

.invalid-value {
  border: 1px solid red;
  box-shadow: 0 0 2px 2px #ddd;
}

.valid-value {
  border: 1px solid var(--btn-color);
  box-shadow: 0 0 2px 2px #ddd;
}

.hidden {
  display: none;
}

@media (width <= 768px) {
  .wrapper {
    max-width: 90%;
  }
}

